<template>
  <label class="lys-radio" :class="{ 'is-checked': label === model }">
    <span class="lys-radio__input">
      <span class="lys-radio__inner"></span>
      <input
      type="radio"
      class="lys-radio__original"
      :value="label"
      :name="name"
      v-model="model"
      >
    </span>
    <span class="lys-radio__label">
      <slot>{{ label }}</slot>
    </span>
  </label>
</template>
<script>
export default {
  name: 'LysRadio',
  inject: {
    RadioGroup: {
      default: ''
    }
  },
  props: {
    label: {
      type: [Number, Boolean, String],
      default: ''
    },
    value: null,
    name: {
      type: String,
      default: ''
    }
  },
  computed: {
    // ååæ°æ®ç»å®è®¡ç®å±æ§
    model: {
      get () {
        // è®¾ç½®å¼ this.RadioGroup.value
        return this.isGroup ? this.RadioGroup.value : this.value
      },
      set (value) { // è§¦å input äºä»¶æ¹åç¶ç»ä»¶çå¼
        this.isGroup ? this.RadioGroup.$emit('input', value) : this.$emit('input', value)
      }
    },
    isGroup () {
      // å¤æ­ radio æ¯å¦è¢« radiogroup åè£¹
      return !!this.RadioGroup
    }
  }
}
</script>

